@import url(https://fonts.googleapis.com/css?family=Titillium+Web:400,700,600);
body {
  background: rgb(2,0,36);
background: -webkit-gradient(linear, left top, right top, from(rgba(2,0,36,1)), color-stop(53%, rgba(9,121,41,1)), to(rgba(0,212,255,1)));
background: -webkit-linear-gradient(left, rgba(2,0,36,1) 0%, rgba(9,121,41,1) 53%, rgba(0,212,255,1) 100%);
background: -o-linear-gradient(left, rgba(2,0,36,1) 0%, rgba(9,121,41,1) 53%, rgba(0,212,255,1) 100%);
background: linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,121,41,1) 53%, rgba(0,212,255,1) 100%);
  font-family: "Titillium Web", sans-serif;
  font-weight: 400;
  margin:0; font:normal 75% Arial, Helvetica, sans-serif; 
} canvas{ display: block; vertical-align: bottom;

}


#particles-js{
   position:absolute; width: 100%; height: 100%; background-color: #b61924; background-image: url(""); background-repeat: no-repeat; background-size: cover; background-position: 50% 50%;
   }

   .count-particles{
      background: #191996; position: absolute; top: 48px; left: 0; width: 80px; color: #13E8E9; font-size: .8em; text-align: left; text-indent: 4px; line-height: 14px; padding-bottom: 2px; font-family: Helvetica, Arial, sans-serif; font-weight: bold; } .js-count-particles{ font-size: 1.1em; } #stats, .count-particles{ -webkit-user-select: none; margin-top: 5px; margin-left: 5px; } #stats{ border-radius: 3px 3px 0 0; overflow: hidden; } .count-particles{ border-radius: 0 0 3px 3px;
       }



* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
*:active, *:focus {
  border: 0;
  outline: 0;
}

a {
  text-decoration: none;
  display: inline-block;
}

#card {
  width: 30rem;
  margin: auto;
  overflow: hidden;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
          -ms-transform: translateX(-50%) translateY(-50%);
      transform: translateX(-50%) translateY(-50%);
  background-color: transparent;     
     
}

.search--box label {
  position: relative;
  
 
}

.search--box input[type='search'] {
  width: 100%;
  font: 1rem "Titillium Web", sans-serif;
  color: #1a4e8e;
  border: 0;
  padding: .75rem .9375rem .9375rem;
  background: white;
  border-radius: .3125rem .3125rem 0 0;
  text-align: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  
  
}

.notfound {
  background: #fff;
  padding: 1.875rem;
  text-align: center;
}
.notfound h2 {
  font-size: 2rem;
  color: #2368bd;
  margin-bottom: .625rem;
}
.notfound p {
  font-size: 1rem;
  color: #a3b6bb;
}

.github--profile {
  width: 100%;
}
.github--profile__info {
 
  text-align: center;
  padding: 1.875rem .9375rem;
}
.github--profile__info img {
  width: 8rem;
  height: 8rem;
  border-radius: 25%;
  display: block;
}

.github--profile__info img:hover{

  -webkit-transition: .5s ease;

  -o-transition: .5s ease;

  transition: .5s ease;
  -webkit-transform: scale(1.3);
        -ms-transform: scale(1.3);
        transform: scale(1.3);

}

.github--profile__info h2 a {
  font-size: 1.65rem;
  color:lightcyan;
  padding-top: 20px;
}
.github--profile__info h2 a:hover{

  color: lightgreen;
}

.github--profile__info h3 {
  font-size: 1.1885rem;
  color:yellow;
}
.github--profile__state {
  
  text-align: center;
  padding: 1.875rem .9375rem;
  border-radius: 0 0 .3125rem .3125rem;
 

}
.github--profile__state ul {
  direction: ltr;
  display: -ms-flexbox;
  display: flex;
}
.github--profile__state li {
  
  background-color: DodgerBlue;
  background-color: #4CAF50;
    border: none;
    color: white;
    padding: 15px 38px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 12px;
}

.github--profile__state li:hover {
  -webkit-transition: .5s ease;
  -o-transition: .5s ease;
  transition: .5s ease;
  -webkit-transform: scale(1.2);
        -ms-transform: scale(1.2);
        transform: scale(1.2);

}


.github--profile__state li:last-child {
  margin-right: 0;
}
.github--profile__state a {
  color: #fff;
}
.github--profile__state i {
  font-size: 1.5rem;
  font-weight: 700;
  font-style: normal;
  display: block;
}
.github--profile__state span {
  font-size: .844rem;
  letter-spacing: .0625rem;
  color: lightcyan;
}

.github--profile__state span:hover{
 color: yellowgreen;
}

.panda {
  font-size: .9rem;
  font-weight: 400;
  color: #e6ebed;
  position: absolute;
  bottom: 3%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          -ms-transform: translateX(-50%);
      transform: translateX(-50%);
}
.panda a {
  color: white;
}

.panda a:hover {
  color:yellowgreen;
}

#title {

  text-align: center;
  font-family: fantasy;



}


